<template>
  <div>
    <div class="shoujiyemian">
      <div id="top">
        <div id="top1">
          <i v-on:click="goback" class="iconfont icon-fanhui"></i>
          <p id="fonttop">分类</p>
          <router-link tag="i" to="/soushuo" class="iconfont icon-yuanjiaojuxing1"></router-link>
        </div>
      </div>
      <div v-for="item of obj">
        <router-link :to="/guoleiyou/+item.eviceId" tag="div"   id="Lihao_two">
          <div :class="item.colorName" class="Lihao_two1">
            <i :class="item.className" class="iconfont"></i>
          </div>
          <div id="Lihao_two2">
            <p id="fonttwo1">{{item.name}}</p>
            <p id="fonttwo2">{{item.jianjie}}</p>
            <i class="iconfont icon-disclosurearrow1"></i>
          </div>
        </router-link>
      </div>
    
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        obj:'',

      }
    },
    methods:{
      goback(){
        this.$router.go(-1);
      }
    },
    mounted(){
       this.$http.get("static/guoleiyou.json").then(function(res){
          this.obj = JSON.parse(res.bodyText)
      },function(){

      })
    }
  }
</script>
<style scoped>
    body{height:100%;}
    .shoujiyemian{width:750px;position:relative}
    #Lihao_two{width:750px;height:172px;border-bottom:1px solid #c8c7cc;position:relative}
    .Lihao_two1{width:100px;height:100px;border-radius:50%;position:absolute;display:flex;align-items:center;justify-content:center;top:30px;left:20px}
    .yanse1{background-color:#4ab7f9}
    .yanse2{background-color:#f9b707}
    .yanse3{background-color:#ff6767}
    .yanse4{background-color:#89c131}
    #Lihao_two i{font-size:65px;color:#fff}
    #Lihao_two2{width:512px;height:110px;position:relative;top:30px;left:140px}
    #fonttwo1{font-size:27.96px;margin-bottom:18px}
    #fonttwo2{font-size:24px;color:#9b9b9b}
    #Lihao_two2 .icon-disclosurearrow1{position:absolute;font-size:26px;top:42px;right:-84px;color:#c7c7cc}
</style>





 


